.pop-up {
    background-color: hsla(0, 0%, 100%, .9);
    display: flex;
    height: 100%;
    left: 0;
    opacity: 0;
    overflow: auto;
    position: fixed;
    top: 0;
    transition-duration: .4s;
    transition-property: opacity, visibility;
    visibility: hidden;
    width: 100%;
    z-index: 1000
}

.pop-up.active {
    opacity: 1;
    visibility: visible
}

.pop-up .window-container {
    margin: auto;
    padding: 50px;
    position: relative
}

.pop-up .window-container .window {
    background-color: #fff;
    position: relative
}

.pop-up .window-container .window .close-btn {
    cursor: pointer;
    height: 18px;
    position: absolute;
    right: -20px;
    top: -20px;
    width: 18px;
    z-index: 1
}

.pop-up .window-container .window .close-btn:after,
.pop-up .window-container .window .close-btn:before {
    background-color: #7a8190;
    content: "";
    height: 2px;
    left: -1px;
    position: absolute;
    top: 8px;
    width: 21px
}

.pop-up .window-container .window .close-btn:before {
    transform: rotate(45deg)
}

.pop-up .window-container .window .close-btn:after {
    transform: rotate(-45deg)
}

.pop-up .window-container .window .close-btn:hover:after,
.pop-up .window-container .window .close-btn:hover:before {
    background-color: #000
}

